import { Component } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { dobViewModel } from "src/app/models/view/dob.viewmodel";
import { CommonService } from "src/app/services/common.service";
import { LogHandlerService } from "src/app/services/log-handler.service";

@Component({
  selector: "app-dob",
  templateUrl: "./dob.component.html",
  styleUrls: ["./dob.component.scss"],
})
export class DobComponent extends BaseComponent<dobViewModel> {
  dobViewModel: dobViewModel = new dobViewModel();

  constructor(commonService: CommonService, logService: LogHandlerService) {
    super(commonService, logService);
  }

  ngOnInit() {
    setInterval(() => {
      this.dobViewModel.todaysDate = new Date();
      this.dobViewModel.currentDate =
        this.dobViewModel.todaysDate.toDateString() +
        " " +
        this.dobViewModel.todaysDate.toTimeString().split(" ")[0];
    }, 1000);
  }

  calculateAge() {
    const birthDate = new Date(this.dobViewModel.birthDate);
    const currentDate = new Date();
    const yearDiff = currentDate.getFullYear() - birthDate.getFullYear();
    const monthDiff = currentDate.getMonth() - birthDate.getMonth();
    const dayDiff = currentDate.getDate() - birthDate.getDate();
    this.dobViewModel.age = `${yearDiff} Years, ${monthDiff} Months and ${dayDiff} Days Old. `;
  }

  resetValues() {
    (this.dobViewModel.age = ""),
      (this.dobViewModel.birthDate = new Date("12/12/2023"));
  }
}
