import { Component } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { timezoneViewModel } from "src/app/models/view/timezone.viewmodel";
import { CommonService } from "src/app/services/common.service";
import { LogHandlerService } from "src/app/services/log-handler.service";
import { TimeZoneConversionService } from "src/app/services/timezone.service";
import * as moment from "moment";

// Now you can use 'moment' as a local variable within this module

@Component({
  selector: "app-timezone",
  templateUrl: "./timezone.component.html",
  styleUrls: ["./timezone.component.scss"],
})
export class TimezoneComponent extends BaseComponent<timezoneViewModel> {
  timezoneViewModel: timezoneViewModel = new timezoneViewModel();
  constructor(
    private timeZoneConversionService: TimeZoneConversionService,
    commonService: CommonService,
    loghandlerService: LogHandlerService
  ) {
    super(commonService, loghandlerService);
    this.timezoneViewModel.allTimeZones =
      this.timeZoneConversionService.getAllTimeZones();
  }

  ngOnInit() {
    // this._commonService.showHideHomeButton = true;
    //getting list of timezones
    this.timezoneViewModel.allTimeZones =
      this.timeZoneConversionService.getAllTimeZones();
    // gett time
    this.getTimeForAllTimeZones();

    //
    this.timezoneViewModel.originalAllTimeZones = [
      ...this.timezoneViewModel.allTimeZoneArray,
    ];

    this.timezoneViewModel.intervalId = setInterval(() => {
      this.updateTime();
    }, 1000);
  }

  ngOnDestroy() {
    clearInterval(this.timezoneViewModel.intervalId);
  }

  //////
  getTimeForAllTimeZones() {
    this.timezoneViewModel.allTimeZones.forEach((element) => {
      moment.tz.setDefault(element);
      const currentTime = moment().toString();
      this.timezoneViewModel.time.push(currentTime);
      this.timezoneViewModel.name.push(element);
    });

    this.timezoneViewModel.allTimeZoneArray = this.timezoneViewModel.name.map(
      (name, index) => ({
        name,
        time: this.timezoneViewModel.time[index],
      })
    );
  }
  ///search
  onInputChange() {
    if (this.timezoneViewModel.searchInput.trim() !== "") {
      this.timezoneViewModel.allTimeZoneArray =
        this.timezoneViewModel.originalAllTimeZones.filter((item: any) => {
          return item.name
            .toLowerCase()
            .includes(this.timezoneViewModel.searchInput.toLowerCase());
        });
    } else {
      this.timezoneViewModel.allTimeZoneArray = [
        ...this.timezoneViewModel.originalAllTimeZones,
      ];
    }
  }
  ////updtae time //// clock running
  updateTime() {
    this.timezoneViewModel.allTimeZoneArray.forEach((item: any) => {
      moment.tz.setDefault(item.name);
      item.time = moment().toString();
    });
  }
}
